.feature {
  display: flex;
  align-items: center;
  flex-direction: column;

  text-align: center;

  height: 100%;

  padding: rem(60) rem(30) rem(45);

  border: 1px solid $light;
  border-radius: $radius;

  @include xl {
    padding: rem(40) rem(20); }

  @include lg {
    padding: rem(20); }

  &__text {
    flex-grow: 1; }

  &__icon {
    // font-size: rem(120)
    width: rem(120);
    margin-bottom: rem(25);
    @include md {
      width: rem(100); }

    @include xs {
      width: rem(80);
      margin-bottom: rem(15); }

    @include xxs {
      width: rem(60); } } }
