.aside {
  background-color: $white;
  border-radius: $radius-sm;
  &__block {
    @include notlast {
      margin-bottom: rem(30); } }

  &__close {
    color: $white;
    top: rem(20);
    right: rem(20);

    @include mdmin {
      display: none; }

    @include hover-focus {
      color: $c-text; } } }

.aside-block {
  border-radius: $radius-sm;
  overflow: hidden;
  border: 1px solid  $light;

  &__header {
    background-color: $accent;
    color: $white;
    text-align: center;

    padding: rem(12);

    font-weight: 700;
    text-transform: uppercase; }

  &__map {
    padding-top: 60%; }

  &--no-border {
    border: 0; } }

.aside-categories {
  &__title,
  &__subitem {
    padding: rem(12) rem(20);
    @include tr(color .3s);

    @include lg {
      padding: rem(10) rem(15); }

    @include hover-focus {
      color: $accent; } }

  &__item {
    @include notlast {
      margin-bottom: 1px; } }

  &__title {
    width: 100%;
    padding-right: rem(35);

    text-align: left;
    text-transform: uppercase;
    background-color: $lighten;
    position: relative;

    @include text-overflow;

    @include lg {
      padding-right: rem(30); }

    @include before {
      @include icon-arrow(.6, .6, .1, currentColor, -45);
      display: block;
      position: absolute;
      z-index: 1;
      top: 50%;
      right: rem(20);
      transform: translate(0, -50%) rotate(-45deg); }

    &--active {
      @include before {
        transform: translate(0, -50%) rotate(135deg);
        color: $accent; } } }

  &__subitem {
    @include notlast {
      border-bottom: 1px solid $light; } }

  &__sublist {
    max-height: 0;
    overflow: hidden;

    &--open {
      max-height: none; } } }

